'use strict';
/**
 * @api LOGIN() LOGIN
 * @apiGroup APP
 * @apiVersion 1.0.0
 * @apiDescription 跳转 app 登录页
 * @apiUse APPNpm
 * @apiUse APPJs
 * @apiParamExample {string} 例子
 * LOGIN()
 * @apiParamExample {string} 返回值
 * 触发 LOGIN_EVENT_ADD中 的事件
 **/

/**
 * todo LOGIN
 */
import {appError, WinJSDec} from './APP/config';

var name = 'LOGIN';
var LOGIN = (fn=()=>{}) => {
  WinJSDec(_ => {
    window.WinJSBridge.call('login', 'login',function (res) {
      var {code, response} = res;
      if (code == 0) {
        fn(response);
      } else {
        throw new Error(`[${name}] ${appError}`);
      }
    });
  });
};
export default LOGIN;